					<view class="d-flex" @click="show('attr')">
						<text class="text-muted mr-2">已选</text>
						<text>火焰红 8GB 256G</text>
					</view>
					<view class="d-flex" @click="show('express')">
						<text class="text-muted mr-2">配送</text>
						<text class="mr-2">河南 郑州</text>
						<text class="main-text-color">现配</text>
					</view>
				</uniListItem>
				<uniListItem showArrow extraStyle="width:5%;" @click="show('service')">
					<view class="d-flex a-center flex-wrap">
						<view class="text-muted font d-flex a-center mr-2">
							<view class="iconfont icon-dui1 main-text-color"></view>
							<view class="d-flex flex-shrink">小米自营</view>
						</view>
						<view class="text-muted font d-flex a-center mr-2">
							<view class="iconfont icon-dui1 main-text-color"></view>
							<view class="d-flex flex-shrink">小米发货</view>
						</view>
						<view class="text-muted font d-flex a-center mr-2">
							<view class="iconfont icon-dui1 main-text-color"></view>
							<view class="d-flex flex-shrink">七天无理由退换</view>
						</view>
					</view>
				</uniListItem>
			</view>
		</view>
		<!-- 横向评论 -->
		<scrollComments :comments="comments" />
		<!-- 商品详情 -->
		<view class="py-2"><uParse className="u-Parse" :content="contextHtml" @preview="preview" @navigate="navigate"></uParse></view>
		<!-- 热门推荐 -->
		<card headTitle="猜你喜欢" :headTitleWWeight="false" :headBorderBottom="false">
			<view class="row j-sb"><commonList v-for="(item, index) in loveList" :key="index" :item="item" :index="index"></commonList></view>
		</card>
		<!-- 底部操作 -->
		<bottomBtn @show="show('attr')"></bottomBtn>
		<!--模态框 弹出框 -->
		<commonPopup :popupClass="Tpopup.attr" @hide="hide('attr')">
			<!-- 商品图片 -->
			<view class="d-flex a-center" style="height: 275rpx;">
				<image src="/static/images/s6.jpg" mode="widthFix" style="width: 180rpx; height: 180rpx;" class="border rounded"></image>
				<view class="pl-2">
					<price priceSize="font-lg" unitSize="font">1399</price>
					<text class="d-block">火焰红 64GB 标配</text>
				</view>
			</view>
			<!-- 表单 -->
			<scroll-view scroll-y class="w-100" style="height: 660rpx;">
				<card :headTitle="item.title" :headTitleWWeight="false" :headBorderBottom="false" v-for="(item, index) in selects" :key="index">
					<ZradioGroup :radioLabel="item" :selected.sync="item.selected"></ZradioGroup>
				</card>
				<view class="d-flex j-sb a-center p-2 border-top border-light-secondary">
					<text>购买数量</text>
					<uniNumberBox :min="1" :value="details.num" @change="details.num = $event"></uniNumberBox>
				</view>
			</scroll-view>
			<!-- 按钮 -->
			<view
				class="main-bg-color text-white font-md d-flex j-center a-center"
				style="height: 100rpx; margin-left: -30rpx; margin-right: -30rpx;"
				hover-class="main-bg-hover-color"
				@tap.stop="addCart"
			>
				加入购物车
			</view>
		</commonPopup>
		<!-- 收货地址 -->
		<commonPopup :popupClass="Tpopup.express" @hide="hide('express')">
			<view class="d-flex a-center j-center font-md border-bottom border-light-secondary" style="height: 100rpx;">收货地址</view>
			<scroll-view scroll-y class="w-100" style="height: 835rpx;">
				<uniListItem v-for="(item,index) in pathList" :key="index">
					<view class="iconfont icon-dingwei font-weight font-md">{{item.name}}</view>
					<view class="font text-light-muted">{{item.path}}{{item.detailPath}}</view>
				</uniListItem>
			</scroll-view>
			<!-- 按钮 -->
			<view
				class="main-bg-color text-white font-md d-flex j-center a-center"
				style="height: 100rpx; margin-left: -30rpx; margin-right: -30rpx;"
				hover-class="main-bg-hover-color"
				@tap.stop="openCreatePath"
			>
				选择新地址
			</view>
		</commonPopup>
		<!-- 服务说明 -->
		<commonPopup :popupClass="Tpopup.service" @hide="hide('service')">
			<view class="d-flex a-center j-center font-md border-bottom border-light-secondary" style="height: 100rpx;">服务说明</view>
			<scroll-view scroll-y class="w-100" style="height: 835rpx;">
				<view class="py-1">
					<view class="d-flex a-center">
						<view class="iconfont icon-dui1 main-text-color mr-1"></view>
						小米自营
					</view>
					<text class="text-light-muted">全场不包邮</text>
				</view>
				<view class="py-1">
					<view class="d-flex a-center">
						<view class="iconfont icon-dui1 main-text-color mr-1"></view>
						小米自营
					</view>
					<text class="text-light-muted">全场不包邮</text>
				</view>
				<view class="py-1">
					<view class="d-flex a-center">
						<view class="iconfont icon-dui1 main-text-color mr-1"></view>
						小米自营
					</view>
					<text class="text-light-muted">全场不包邮</text>
				</view>
			</scroll-view>
			<!-- 按钮 -->
			<view
				class="main-bg-color text-white font-md d-flex j-center a-center"
				style="height: 100rpx; margin-left: -30rpx; margin-right: -30rpx;"
				hover-class="main-bg-hover-color"
				@tap.stop="addCart"
			>
				加入购物车
			</view>
		</commonPopup>
	</view>
</template>

<script>
import uParse from '../../components/uni-ui/uParse/src/wxParse.vue';
import {mapState,mapMutations} from 'vuex';

import swiperImage from '@/components/index/index-swiper.vue';
import baseInfo from '@/components/detail/base-info.vue';
import scrollAttr from '@/components/detail/scroll-attr.vue';
import uniListItem from '@/components/uni-ui/uni-list-item/uni-list-item.vue';
import scrollComments from '@/components/detail/scroll-comments.vue';
import card from '@/components/common/card.vue';
import commonList from '@/components/common/commonList.vue';
import bottomBtn from '@/components/detail/bottom-btn.vue';
import commonPopup from '@/components/common/common-popup.vue';
import price from '@/components/common/price.vue';
import ZradioGroup from '@/components/common/radio-group.vue';
import uniNumberBox from '@/components/uni-ui/uni-number-box/uni-number-box.vue';

let htmlString = `<p>
			<img src="https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/22c1c7124e7d80049f108e36b7beb6cb.jpg?w=1080&h=1700&bg=20204">
			<img src="https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/da5125cf669159eb2c46eb8728779b0e.jpg?w=1080&h=1453&bg=FFFFFF">
			<img src="https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/006ca0e31eb01237a2d9630f47195dec.jpg?w=1080&h=960&bg=D4B072">
			<img src="https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/c20e02f4eab3f0469f2b9b9dd5c4812a.jpg?w=1080&h=1321&bg=DDDDDD">
		</p>`;

export default {
	components: {
		uParse,
		swiperImage,
		baseInfo,
		scrollAttr,
		uniListItem,
		scrollComments,
		card,
		commonList,
		bottomBtn,
		commonPopup,
		price,
		ZradioGroup,
		uniNumberBox
	},
	data() {
		return {
			selects: [
				{
					title: '颜色',
					selected: 0,
					list: [{ name: '火焰红' }, { name: '珊瑚蓝' }, { name: '珍珠白' }]
				},
				{
					title: '容量',
					selected: 0,
					list: [{ name: '128GB' }, { name: '256GB' }, { name: '520GB' }]
				},
				{
					title: '套餐',
					selected: 0,
					list: [{ name: '标配' }, { name: '套餐一' }, { name: '套餐二' }]
				}
			],
			Tpopup: {
				attr: 'none',
				express: 'none',
				service: 'none'
			},
			contextHtml: htmlString,
			comments: [
				{
					userpic: '/static/images/logo.png',
					username: '小米粉',
					createTtime: '2020-12-01',
					goodTnum: 666,
					context: '评论内容',
					imageList: ['/static/images/logo.png', '/static/images/logo.png', '/static/images/logo.png']
				},
				{
					userpic: '/static/images/logo.png',
					username: '小米粉',
					createTtime: '2020-12-01',
					goodTnum: 666,
					context: '评论内容',
					imageList: ['/static/images/logo.png', '/static/images/logo.png', '/static/images/logo.png']
				},
				{
					userpic: '/static/images/logo.png',
					username: '小米粉',
					createTtime: '2020-12-01',
					goodTnum: 666,
					context: '评论内容',
					imageList: ['/static/images/logo.png', '/static/images/logo.png', '/static/images/logo.png']
				}
			],
			banners: [{ src: '/static/images/img1.jpg' }, { src: '/static/images/img2.jpg' }],
			details: {
				id:20,
				title: 'Redmi Note 9',
				desc:"一亿像素夜景相机 \/ 国内首发骁龙 750G处理器 \/ 6.67'' 小孔径全面屏 \/ 120Hz六档变速高刷屏 \/ 全场景AI四摄 \/ 线性马达 \/ 4820mAh+33W闪充 \/ 立体声双扬声器 \/ 多功能NFC",
				cover:'/static/images/s8.jpg',
				pprice: 1999,
				num: 1,
				max: 100
			},
			baseAttrs: [
				{ iconTu: 'icon-cpu_icon', title: 'CPU', desc: '骁龙 750G' },
				{ iconTu: 'icon-cpu_icon', title: 'CPU', desc: '骁龙 750G' },
				{ iconTu: 'icon-cpu_icon', title: 'CPU', desc: '骁龙 750G' },
				{ iconTu: 'icon-cpu_icon', title: 'CPU', desc: '骁龙 750G' },
				{ iconTu: 'icon-cpu_icon', title: 'CPU', desc: '骁龙 750G' },
				{ iconTu: 'icon-cpu_icon', title: 'CPU', desc: '骁龙 750G' },
				{ iconTu: 'icon-cpu_icon', title: 'CPU', desc: '骁龙 850G' }
			],
			loveList: [
				{
					cover: '/static/images/s6.jpg',
					title: 'Redmi K30 4G',
					desc: '120Hz流速屏，全速热爱',
					oprice: 2699,
					nprice: 1399
				},
				{
					cover: '/static/images/s7.jpg',
					title: 'Redmi K30 4G',
					desc: '120Hz流速屏，全速热爱',
					oprice: 2699,
					nprice: 1399
				},
				{
					cover: '/static/images/s8.jpg',
					title: 'Redmi K30 4G',
					desc: '120Hz流速屏，全速热爱',
					oprice: 2699,
					nprice: 1399
				},
				{
					cover: '/static/images/s9.jpg',
					title: 'Redmi K30 4G',
					desc: '120Hz流速屏，全速热爱',
					oprice: 2699,
					nprice: 1399
				}
			]
		};
	},
	// 监听页面返回事件
	onBackPress() {
		//****** 当返回true时，阻止了返回键的默认事件，此前进行关闭模态框
		for (let key in this.Tpopup) {
			if (this.Tpopup[key] !== 'none') {
				this.hide(key);
				return true;
			}
		}
	},
	computed:{
		...mapState({
			pathList:state => state.path.list
		})
	},
	methods: {
		...mapMutations([
			'addGoodsToCart'
		]),
		addCart(){ //加入购车
			//组织数据
			let goods = this.details;
			goods['checked'] = false;
			goods['attrs'] = this.selects;
			//加入购物车
			this.addGoodsToCart(goods);
			//隐藏筛选框
			this.hide('attr');
			//成功的提示
			uni.showToast({
				title: '加入成功'
			});
		},
		openCreatePath(){
			uni.navigateTo({
				url:'../user-path-edit/user-path-edit'
			})
			this.hide('express')
		},
		hide(key) {
			this.Tpopup[key] = 'hide';
			setTimeout(() => {
				this.Tpopup[key] = 'none';
			}, 200);
		},
		show(key) {
			this.Tpopup[key] = 'show';
		},
		preview(src, e) {
			//do something
			console.log('src:' + src);
		},
		navigate(href, e) {
			//如允许点击超链接跳转，则应该打开一个新页面，并传入href，由新页面内嵌webview组件负责显示该链接内容
			console.log('href:' + href);
			uni.showModal({
				content: '点击链接为:' + href,
				showCancel: false
			});
		}
	}
};
</script>

<style>
.u-Parse view {
	line-height: 0 !important;
}
.u-Parse .p {
	padding: 0 !important;
}
</style>
